<template>
    <div class="dashboard">
      <h1>网站数据一览表</h1>
      <div class="card-container">
        <CardComponent title="网站注册量" :count="registeredUsers" />
        <CardComponent title="志愿者招募信息数量" :count="volunteerRecruitment" />
        <CardComponent title="工作招聘数量" :count="jobPostings" />
        <CardComponent title="失物招领数量" :count="lostAndFound" />
        <CardComponent title="其他信息数量" :count="otherInfo" />
        <CardComponent title="反馈信息数" :count="feedbackCount" />
        <CardComponent title="紧急通知数量" :count="emergencyNotices" />
        <CardComponent title="社区活动数量" :count="communityEvents" /> <!-- 修改这里 -->
        <CardComponent title="安全专栏数量" :count="safetyColumns" />
      </div>
    </div>
  </template>
  
  <script>
import axios from 'axios';
import CardComponent from '../CardComponent.vue';

export default {
  components: {
    CardComponent,
  },
  data() {
    return {
      registeredUsers: 0,
      volunteerRecruitment: 0,
      jobPostings: 0,
      lostAndFound: 0,
      otherInfo: 0,
      feedbackCount: 0,
      emergencyNotices: 0,
      communityEvents: 0, 
      safetyColumns: 0,
    };
  },
  mounted() {
    this.fetchCounts();
  },
  methods: {
    async fetchCounts() {
      try {
        const token = localStorage.getItem('token');
        if (!token) {
          console.error('No token found. Redirecting to login.');
          this.$router.push('/login');
          return;
        }

        // 设置 Axios 默认 Authorization 头
        axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;

        // Fetch feedback count
        const feedbackResponse = await axios.get('http://localhost:8090/fcount');
        this.feedbackCount = feedbackResponse.data.count;

        // Fetch registered user count
        const personResponse = await axios.get('http://localhost:8090/personcount');
        this.registeredUsers = personResponse.data.count;

        // Fetch message count by type
        const messageResponse = await axios.get('http://localhost:8090/messagecount');
        messageResponse.data.forEach(item => {
          switch (item.type) {
            case '志愿者招募':
              this.volunteerRecruitment = item.count;
              break;
            case '工作招聘':
              this.jobPostings = item.count;
              break;
            case '失物招领':
              this.lostAndFound = item.count;
              break;
            case '其他事项':
              this.otherInfo = item.count;
              break;
            case '紧急通知':
              this.emergencyNotices = item.count;
              break;
            case '社区活动':
              this.communityEvents = item.count;
              break;
            case '安全专栏':
              this.safetyColumns = item.count;
              break;
            default:
              break;
          }
        });

      } catch (error) {
        console.error('Error fetching data:', error);
      }
    },
  },
};
</script>

  
  <style scoped>
  .dashboard {
    padding: 20px;
    text-align: center;
  }
  
  .card-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    margin-top: 20px;
  }
  </style>
  
  